        // console.dir(res)
        this.Transfe_seat.day.fail = res.data.CallinMiss // 转坐席失败
        this.Transfe_seat.day.Success = res.data.CallinSucc // 转坐席成功
        this.Transfe_seat.day.Total = parseInt(res.data.CallinSucc) + parseInt(res.data.CallinMiss)
        console.dir(this.Transfe_seat)
      }

      //
    })
    this.GetData().then(res => {
      // this.initEcharts()
      // this.InitCallSituation(this.$echarts.init(this.$refs.CallSituation))
    }) // 加载 数据 Echarts显示
