    renderItem(params, api) {
      var categoryIndex = api.value(0)
      var start = api.coord([api.value(1), categoryIndex])
      var end = api.coord([api.value(2), categoryIndex])
      var height = api.size([0, 1])[1] * 0.6
      var rectShape = this.$echarts.graphic.clipRectByRect(
        {
          x: start[0],
          y: start[1] - height / 2,
          width: end[0] - start[0],
          height: height
        },
        {
          x: params.coordSys.x,
          y: params.coordSys.y,
          width: params.coordSys.width,
          height: params.coordSys.height
        }
      )

      return (
        rectShape && {
          type: 'rect',
          shape: rectShape,
          style: api.style()
        }
      )
    },
    initEcharts() {
      // console.dir(this.Chart_data)
      // var startTime = +new Date();
      var startTime = this.$moment([2020, 4 - 1, 9, 0, 0, 0]).unix() * 1000
      var categories = ['4401', '4402', '4403', '4404']
      var option = {
        tooltip: {
          // 鼠标悬浮提示     格式化器
          formatter: function(params) {
            return params.marker + params.name + ': ' + params.value[3] + ' ms'
          }
        },
        title: {
          // 标题
          text: 'Test view',
          left: 'center'
        }, // 滑动条型数据区域缩放组件
        dataZoom: [
          {
            type: 'slider',
            filterMode: 'weakFilter',
            showDataShadow: true, // 显示走势
            bottom: '5%', // '10%'
            height: 8, // 拖动滚动条高度 以及位置
            borderColor: 'transparent',
            backgroundColor: '#e2e2e2',
            handleIcon: 'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7v-1.2h6.6z M13.3,22H6.7v-1.2h6.6z M13.3,19.6H6.7v-1.2h6.6z', // jshint ignore:line
            handleSize: 8, // 图标大小 图标样式
            handleStyle: {
              shadowBlur: 6,
              shadowOffsetX: 1,
              shadowOffsetY: 2,
              shadowColor: '#aaa'
            }, // 设置 两端样式
            labelFormatter: ''
          },
          {
            type: 'inside',
            filterMode: 'weakFilter'
          }
        ],
        grid: {
          // height: 150
        },
        xAxis: {
          min: startTime, // 坐标轴刻度最小值
          scale: true,
          axisLabel: {
            // 坐标轴刻度标签的相关设置
            formatter: function(val) {
              var date = new Date(val)
              // var Y = date.getFullYear() + '-'
              // var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
              // var D = date.getDate() + ' '
              var h = date.getHours() + ':'
              var m = date.getMinutes() + ':'
              var s = date.getSeconds()
              // console.dir(Y + M + D + h + m + s)
              return h + m + s
            }
          }
        },
        yAxis: {
          // y 轴
          data: categories,
          axisLabel: {
            rotate: 20, // 旋转的角度
            show: true,
            interval: 0 //  强制显示所有标签
          }
        },
        series: [
          {
            type: 'custom', // 通过 type 决定自己的图表类型
            renderItem: this.renderItem,
            itemStyle: {
              opacity: 0.8
              // 图形透明度
            },
            encode: {
              x: [1, 2],
              y: 0
            },
            data: this.Chart_data
          }
        ]
      }

      // 使用刚指定的配置项和数据显示图表。
      this.$echarts.init(this.$refs.test).setOption(option)

      // console.dir(Chart_data);
      console.dir(startTime)
    },
    InitCallSituation(CallSituationChart) {
      var option = {
        color: ['#d4d1d4', '#a0e5fe', '#ff9b3d', '#b4d93f'], // , '#fe5e61'
        legend: {},
        tooltip: {},
        dataset: {
          dimensions: ['product', '转坐席失败数', '转坐席成功数', '外呼失败数', '外呼成功数'],
          source: this.CallSituationChartData
        },
        xAxis: { type: 'category' },
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [
          {
            type: 'bar',
            label: {
              show: true,
              position: 'top'
            }
          },
          {
            type: 'bar',
            label: {
              show: true,
              position: 'top'
            }
          },
          {
            type: 'bar',
            label: {
              show: true,
              position: 'top'
            }
          },
          {
            type: 'bar',
            label: {
              show: true,
              position: 'top'
            }
          },
          {
            type: 'bar',
            label: {
              show: true,
              position: 'top'
            }
          }
        ]
      }
      // 使用刚指定的配置项和数据显示图表。
      CallSituationChart.setOption(option)
    },
    EcharResize(EchartsName) {
      const _this = this
      console.dir(EchartsName)
      window.onresize = function() {
        EchartsName.forEach(element => {
          _this.$echarts.init(element).resize()
        })
      }
    },
    AxiosUpdate(requestUrl) {
      // const _this = this
      return this.$axios({
        method: 'get',
        url: requestUrl
      })
    }
  },
  // 实例被挂载后调用
  mounted() {
    // this.EcharResize([this.$refs.CallSituation, this.$refs.test])
    this.EcharResize([this.$refs.CallSituation])
    setInterval(() => {
      var rateChart = this.$echarts.init(this.$refs.test)
      rateChart.showLoading() // 显示loading
      rateChart.setOption({
        series: [
          {
            data: this.Chart_data
          }
        ]
      })
      rateChart.hideLoading() // 隐藏loading
      // if (rateChart != null && rateChart != "" && rateChart != undefined) {
      //     rateChart.dispose(); //销毁
      // }
    }, 1000 * 60 * 60)
    setTimeout(() => {
      var rateChart = this.$echarts.init(this.$refs.CallSituation)
      rateChart.showLoading() // 显示loading
      setTimeout(() => {
        const _this = this
        this.AxiosUpdate('/api')
          .then(res => {
            console.dir(_this.CallSituationChartData)
            // _this.CallSituationChartData.push(['12:00', 72.4, 53.9, 39.1, 78, 20])
            rateChart.setOption({
              dataset: {
                source: _this.CallSituationChartData
              }
            })
            rateChart.hideLoading() // 隐藏loading
          })
          .catch(err => {
            console.dir(err)
          })
      }, 1000)

      // this.table_Data.push({
      //   date: '2020-02-03',
      //   name: '王小虎',
      //   address: '湖北武汉'
      // })
    }, 1000 * 60 * 60)
  }
}
</script>

